import React, { Component } from 'react';
import QRCode from 'react-qr';
import Countdown from './countdown.js'
import ToggleDisplay from 'react-toggle-display';

class stage3 extends Component {
  constructor( { cb3 }) {
    super();
    this.state = cb3;
    this.Ts1 = false;
    this.Ts2 = false;
    this.Ts3 = false;
    this.Ts4 = false;
    this.TsE = false;
    if( this.state.TxState === 1 ){
      this.TxStateV = "Active";
      this.Ts1 = true;
    }else if( this.state.TxState === 2){
      this.TxStateV = "Completed";
      this.Ts2 = true;
    }else if( this.state.TxState === 3){
      this.TxStateV = "Expired";
      this.Ts3 = true;
    }else if( this.state.TxState === 4){
      this.TxStateV = "Overflow"
      this.Ts4 = true;
    }else if( this.state.stage4 === true){
      this.TxStateV = "Error Detected"
      this.TsE = true;
    }
  }
  render() {
    console.log(this.state.Exchange);
    return (
        <div className="form-group">
          <ToggleDisplay if={this.Ts1} >
            <h1>Stage 2 - Transfer</h1>
            <h3>State : {this.TxStateV}</h3>
            <h3> TxID </h3>
            <p className="text-muted"> <a href={this.state.Txid}> {this.state.Txid} </a> </p>
            <h5>Rate applicable : {this.state.Rate}</h5>
            <h5>Address to Transfer {this.state.Exchange.split('/')[1]}</h5>
            <h5>{this.state.TxAddr}</h5>
            <h5>
              <QRCode text={this.state.TxAddr}/>
            </h5>
            <h3>Amount of {this.state.Exchange.split('/')[0]} to Transfer</h3>
            <h5> {this.state.C1Value} </h5>
            <h3>Amount of {this.state.Exchange.split('/')[1]} to Receive</h3>
            <h5> {this.state.C2Value} </h5>
            <h3>Time Remaining</h3>
            <h5>
              <Countdown date={this.state.EndDate} />
            </h5>
          </ToggleDisplay>
          <ToggleDisplay if={this.Ts2} >
            <h1>Transfer Status</h1>
            <h5>State : {this.TxStateV}</h5>
            <h5>Rate applicable : {this.state.Rate}</h5>
            <h5>Transaction Hash : { this.state.OutHash } </h5>
            <h3>Amount of {this.state.Exchange.split('/')[0]} to Transfered</h3>
            <h5> {this.state.C1Value} </h5>
            <h3>Amount of {this.state.Exchange.split('/')[1]} to Received</h3>
            <h5> {this.state.C2Value} </h5>
          </ToggleDisplay>
          <ToggleDisplay if={this.Ts3} >
            <h1>Transfer Status</h1>
            <h5>State : {this.TxStateV}</h5>
            <h5>Rate applicable : {this.state.Rate}</h5>
            <h3>Amount of {this.state.Exchange.split('/')[0]} to be Transfered</h3>
            <h5> {this.state.C1Value} </h5>
            <h3>Amount of {this.state.Exchange.split('/')[1]} to be Received</h3>
            <h5> {this.state.C2Value} </h5>
          </ToggleDisplay>
          <ToggleDisplay if={this.Ts4} >
            <h1>Transfer Status</h1>
            <h5>State : {this.TxStateV}</h5>
            <h3>Overflow Detected contact support.</h3>
          </ToggleDisplay>
          <ToggleDisplay if={this.TsE} >
            <h1>Opps Some thing went Wrong</h1>
            <p className="text-muted">Please Contact <a href="Support@sikka.market"> Support </a> </p>
          </ToggleDisplay>
        </div>
    )
  }
}
export default stage3;



